import React, { Component } from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { connect } from 'react-redux';
import styles from '../styles/IconMenuSettingsContext.module.css';
import {
  setCategories,
  setIconMenuShortcut,
  setPieMenuItemsAlignedHorizontally,
  setTransparentBackground,
  setPieMenuCenteredToView,
} from '../actions/main';
import Checkbox from './Checkbox';
import Button from './Button';
import CategoryOrderingContainer from './CategoryOrderingContainer';

class IconMenuSettingsContext extends Component {
  constructor(props) {
    super(props);
    this.state = {
      iconKey: [0, 0, 0, null],
    };

    this.handleOnKeyDown = this.handleOnKeyDown.bind(this);
    this.handleOnKeyUp = this.handleOnKeyUp.bind(this);
    this.handleResetDefaultsClick = this.handleResetDefaultsClick.bind(this);
  }

  componentDidMount() {
    this.setState({ iconKey: this.props.iconMenuShortcut });
  }

  componentDidUpdate(prevProps) {
    const { iconMenuShortcut } = this.props;
    if (prevProps.iconMenuShortcut !== iconMenuShortcut) {
      this.setState({ iconKey: iconMenuShortcut });
    }
  }

  handleResetDefaultsClick(e) {
    e.preventDefault();
  }

  handleOnKeyDown(e) {
    e.preventDefault();
    let t = [0, 0, 0, null];
    if (e.ctrlKey) {
      t[0] = 1;
    }
    if (e.shiftKey) {
      t[1] = 1;
    }
    if (e.altKey) {
      t[2] = 1;
    }
    let k = e.key.toUpperCase();
    if (k != 'ALT' && k != 'CONTROL' && k != 'SHIFT') {
      t[3] = k;
    }
    this.setState({ iconKey: t });
  }

  handleOnKeyUp(e) {
    e.preventDefault();
    let t = [...this.state.iconKey];
    if (t[3]) {
      this.props.setIconMenuShortcut(t);
      return;
    }
    let k = e.key.toUpperCase();
    if (k === 'CONTROL') {
      t[0] = 0;
    } else if (k === 'SHIFT') {
      t[1] = 0;
    } else if (k === 'ALT') {
      t[2] = 0;
    }
    this.setState({ iconKey: t });
  }

  render() {
    const { pieMenuCenteredToView, transparentBackground, pieMenuItemsAlignedHorizontally } = this.props;
    const { iconKey } = this.state;
    let t = '';
    if (iconKey[0]) {
      t += 'CTRL + ';
    }
    if (iconKey[1]) {
      t += 'SHIFT + ';
    }
    if (iconKey[2]) {
      t += 'ALT + ';
    }
    if (iconKey[3] !== null) {
      t += iconKey[3];
    }
    return (
      <div className={styles.box}>
        <div className={styles.outerContainer + ' ' + styles.outerLeft}>
          <div className={styles.innerContainer}>
            <p className={styles.header}>Keyboard Settings</p>
            <div className={styles.keyboardControlsBox}>
              {/* <p className={styles.text}>Text Menu Key</p>
              <input type="text" className={styles.textInput} /> */}
              <p className={styles.text}>Icon Menu Key</p>
              <input
                type="text"
                className={styles.textInput}
                onKeyDown={this.handleOnKeyDown}
                onKeyUp={this.handleOnKeyUp}
                value={t}
              />
            </div>
            <p className={styles.header}>Display Settings</p>
            <div className={styles.displaySettingsBox}>
              <Checkbox
                containerClass={styles.checkboxContainer}
                onChange={(e) => this.props.setTransparentBackground(!transparentBackground)}
                checked={transparentBackground}
              >
                <p className={styles.checkboxText}>Transparent Background</p>
              </Checkbox>
              <Checkbox
                containerClass={styles.checkboxContainer}
                onChange={(e) => {
                  this.props.setPieMenuCenteredToView(!pieMenuCenteredToView);
                }}
                checked={pieMenuCenteredToView}
              >
                <p className={styles.checkboxText}>Center Pie Menu to Viewport</p>
              </Checkbox>
              <Checkbox
                containerClass={styles.checkboxContainer}
                onChange={(e) => this.props.setPieMenuItemsAlignedHorizontally(!pieMenuItemsAlignedHorizontally)}
                checked={pieMenuItemsAlignedHorizontally}
              >
                <p className={styles.checkboxText}>Align Pie Menu items horizontally</p>
              </Checkbox>
            </div>
            <Button color="sea" className={styles.resetButton} onClick={this.handleResetDefaultsClick}>
              Restore Defaults
            </Button>
          </div>
        </div>
        <div className={styles.outerContainer + ' ' + styles.outerRight}>
          <div className={styles.innerContainer}>
            <p className={styles.header}>Pie Menu Ordering</p>
            <DndProvider backend={HTML5Backend}>
              <CategoryOrderingContainer />
            </DndProvider>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { main } = state;
  return {
    categories: main.categories,
    pieMenuPosition: main.pieMenuPosition,
    pieMenuCenteredToView: main.pieMenuCenteredToView,
    pieMenuItemsAlignedHorizontally: main.pieMenuItemsAlignedHorizontally,
    transparentBackground: main.transparentBackground,
    iconMenuShortcut: main.iconMenuShortcut,
  };
}

const mapDispatchToProps = {
  setCategories,
  setIconMenuShortcut,
  setPieMenuItemsAlignedHorizontally,
  setTransparentBackground,
  setPieMenuCenteredToView,
};

export default connect(mapStateToProps, mapDispatchToProps)(IconMenuSettingsContext);
